<template>
    <div class="row">
      <div class="col-md-12">
        <card header-text="Programas">
          <div class="table-responsive">
            <table class="table table-striped table-sm color-icon-label-table">
              <thead>
              <tr>
                <td align="center">Ação</td>
                <td>Programa</td>
                <!-- <td>Gerência</td>
                <td>Categoria</td> -->
              </tr>
              </thead>
              <tbody>
                <tr v-for="(programa) in dadosProgramas" :key="programa.id" >
                  <td align="center">
                      <button type="button" @click="detalharProjetos" class="btn btn-outline-info btn-sm">
                        <i class="fa fa-search-plus"></i>
                      </button>
                  </td>
                  <td>{{ programa.titulo }}</td>
                  <!-- <td>{{ programa.gerente }}</td>
                  <td>{{ programa.categoria }}</td> -->
                </tr>
              </tbody>
            </table>
          </div>
        </card>
      </div>
    </div>
</template>

<script>    
    import Vue from 'vue'

    import DataTable from '../../components/tables/vuetable-2/DataTable.vue'
    import BadgeColumn from '../../components/tables/BadgeColumn.vue'
    import programasArray from '../../dados/mockdata.js'
    
    Vue.component('badge-column', BadgeColumn);

    import FieldsDef from '../../components/tables/vuetable-2/data/fields-definition';
    import ItemsPerPageDef from '../../components/tables/vuetable-2/data/items-per-page-definition';

    export default {
        name: 'ListPrograma',
        data() {
          return {
            dadosProgramas: null
          }
        },
        created() {
          this.$http.get('programas.json').then(res => {
              Object.entries(res.data).forEach(([key, value]) => {
                  this.dadosProgramas.push({
                      id: key,
                      dados: value
                  });
              });
          })
        },
        components: {
          DataTable
        },
        methods: {
            detalharProjetos: function() {
                this.$router.push('/list-projetos')
            }
        }
    }
</script>

<style lang="scss">
  .table thead tr{
    color: #007bff;
  }
  .error-icon{
    color: #e34a4a;
  }
  .success-icon {
    display: inline-block;
    color: #4ae387;
  }
  .table-sm th, .table-sm td{
    padding: .7rem;
  }
  .color-icon-label-table {
    td:first-child {
      width: 1rem;
    }
  }
</style>


